<template>
	<div class="flex justify-center">
		<div class="w-1/2 bg-blue-900 rounded-lg shadow px-6 py-12 flex flex-col item-center">
			<div class="bg-gray-400 rounded-full flex justify-around p-1">
				<button 
					v-for="(price, frequency) in plans[0].pricing"
					@click="currentFrequency=frequency" 
					class="rounded-full text-xs font-bold px-6 py-1 uppercase focus:outline-none "
					:class="currentFrequency == frequency ? 'bg-blue-600 text-red-400' : ''"
					>{{ frequency }}</button>
				<!-- <button 
					@click="currentFrequency='yearly'" 
					class="rounded-full text-xs font-bold px-6 py-1 uppercase focus:outline-none"
					:class="currentFrequency == 'yearly' ? 'bg-blue-600 text-red-400' : ''"
					>Yearly</button> -->
			</div>

			<div class="pt-8 flex w-full">
				<div v-for="plan in plans" class="text-white w-1/2">
					<h1 class="text-2xl font-bold">{{ plan.name }}</h1>
					<ul class="pt-4">
						<li v-for="benefit in plan.benefits[currentFrequency]">{{ benefit }}</li>
					</ul>
					<div class="flex justify-center pt-8">
						<div class="text-4xl font-bold">{{ getPrice(plan.pricing[currentFrequency].price) }}</div>
						<div class="pl-1 pt-5 text-red-400">{{ plan.pricing[currentFrequency].label }}</div>
					</div>
					
				</div>
<!-- 				<div class="text-white w-1/2">
					<h1 class="text-2xl font-bold">Pro</h1>
					<ul class="pt-4">
						<li>Benfit 1</li>
						<li>Benfit 2</li>
						<li>Benfit 3</li>
						<li>Benfit 4</li>
					</ul>
					<div class="flex justify-center pt-8">
						<div class="text-4xl font-bold">$199</div>
						<div class="pl-1 pt-5 text-red-400">/mo</div>
					</div>
				</div> -->
			</div>	
			<div class="pt-4 text-center text-white text-sm font-bold">
				<a href="#" @click.prevent="currency = 'usd'">USD</a> |
				<a href="#" @click.prevent="currency = 'eur'">EUR</a> |
				<a href="#" @click.prevent="currency = 'uah'">UAH</a>
			</div>		
			
		</div>
	</div>
	
</template>

<script>
	export default {
		data() {
			return {
				currentFrequency: 'monthly',
				currency: 'usd',

				plans: [
					{
						name: 'statrer',
						benefits: {
							monthly: ['Benefit 1', 'Benefit 2', 'Benefit 3'],
							yearly: ["Benefit 1", "Benefit 2", "Benefit 3", "Benefit 4", "Benefit 5"],
							lifetime: ["lifetime 1", "lifetime 2","lifetime 3","lifetime 4", ]
						},
						pricing:  {
							monthly: { price: 99, label: '/mo'},
							yearly: { price: 499, label: '/yr'},
							lifetime: { price: 1200, label: ''},
						}
					},
					{
						name: 'pro',
						benefits: {
							monthly: ['Benefit 1', 'Benefit 2', 'Benefit 3'],
							yearly: ["Benefit 1", "Benefit 2", "Benefit 3", "Benefit 4", "Benefit 5"],
							lifetime: ["lifetime 1", "lifetime 2","lifetime 3","lifetime 4", ]
						},
						pricing:  {
							monthly: { price: 199, label: '/mo'},
							yearly: { price: 899, label: '/yr'},
							lifetime: { price: 2200, label: ''},
						}
					},
				]
			}
		},
		methods: {
			getPrice(price) {
				// return '$' + price
				return this['currency' + this.currency.toUpperCase()](price);
			},
			currencyUSD(price) {
				return '$' + price
			},
			currencyEUR(price) {
				return	Math.ceil((price * 0.85)) + "E"
			},
			currencyUAH(price) {
				return Math.ceil((price * 26)) + '₴'
			}
		}
	}
</script>